.footer {
  background-color: $black-light;
  color: #717171;
  &__inner {
    @include flex(flex-start, center, row, wrap);
    padding-top: 15px;
    @include sm {
      display: block; } }
  &__logo {
    width: percentage(3/12);
    @include md {
      width: 220px; }
    @include sm {
      width: auto;
      margin-bottom: 10px;
      text-align: center; } }
  &__copy {
    width: percentage(6/12);
    text-align: center;
    @include md {
      width: calc(100% - 440px); }
    @include sm {
      width: auto;
      margin-bottom: 10px; } }
  &__contacts {
    width: percentage(3/12);
    @include flex(flex-end);
    color: $white;
    @include md {
      width: 220px; }
    @include sm {
      width: auto;
      display: block;
      text-align: center;
      margin-bottom: 10px; } }
  &__auth {
    width: 100%;
    margin-top: -15px;
    @include flex(center, center);
    @include lg {
      margin-top: 0; }
    a {
      color: $white;
      text-decoration: underline; } }
  &__auth-icon {
    @include flex(center, center);
    background-color: $black;
    color: $white;
    height: 35px;
    width: 35px;
    margin-right: 15px;
    @include xss {
      margin-right: 5px; }
    .icon {
      font-size: 21px; } } }
