.header {
  $this: &;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  background-color: $white;
  color: $c-text-dark;
  @include tr(background-color .3s);
  &__inner {
    height: $header-height;
    @include flex(flex-start, center);
    @include lg {
      height: $header-height-lg; } }
  &__nav {
    flex-grow: 1;
    @include lg {
      position: fixed;
      z-index: 10;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba($black ,.7);
      visibility: hidden;
      opacity: 0;
      @include tr(opacity .3s, visibility .3s);
      &.is-active {
        visibility: visible;
        opacity: 1;
        #{$this}__nav-in {
          transform: translate(0, 0); } } } }
  &__nav-in {
    @include lg {
      display: inline-block;
      vertical-align: middle;
      width: 300px;
      background-color: $white;
      padding: 15px;
      padding-top: 50px;
      overflow: auto;
      height: 100%;
      position: relative;
      transform: translate(-100%, 0);
      @include ready {
        @include tr(transform .3s); } } }
  &__logo {
    @include lg {
      flex-grow: 1; } }
  &__burger {
    display: none;
    margin-left: 15px;
    @include lg {
      display: block; }
    @include xss {
      margin-left: 5px; } }
  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    z-index: 1;
    top: 20px;
    right: 20px;
    display: none;
    @include lg {
      display: block; } }



  &--transp {
    background-color: transparent; } }

.contacts-sm {
  &__phone {
    font-weight: 700; }
  &__btn {
    button, a {
      text-decoration: underline; } } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  // margin-top: -24px
  position: relative;
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0; }
  span:after {
    top: 5px; }
  span:before {
    bottom: 5px; }
  span {
    // position: relative
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; } }



