.btn {
  display: inline-block;
  text-align: center;
  background-color: $accent;
  font-weight: 700;
  color: $white;
  border: 1px solid  $accent;
  border-radius: 2em;
  min-width: 250px;
  padding: .9em;
  @include tr(color .3s, background-color .3s, border-color .3s);
  @include hover {
    color: $accent;
    background-color: transparent; }
  @include xs {
    min-width: 0;
    width: 100%; }

  &--transp {
    background-color: transparent;
    color: $accent;
    @include hover {
      background-color: $accent;
      color: $white; } }
  &--arrow {
    position: relative;
    @include after {
      @include icon-arrow(8, 8, 1, currentColor, 225);
      display: inline-block;
      position: relative;
      margin-left: .2em; } }
  &--no-hover {
    &.btn--transp {
      @include hover {
        background-color: transparent;
        color: $accent; } } }
  &--full-sm {
    @include sm {
      min-width: 0;
      width: 100%; } } }
