body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'Gilroy', Arial, sans-serif;
  font-weight: 300;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm-px; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-animation) {
    animation: fadeIn 1s; } }

.logo {
  display: inline-block;
  font-size: 0;
  img {
    max-height: 100%; }
  @include lg {
    height: $header-height-lg - 5px; } }

.main {
  padding-top: $header-height;
  @include lg {
    padding-top: $header-height-lg; }
  &--bg {
    @include image('bg.jpg');
    background-position: 0 0;
    background-size: 100% auto;
    background-repeat: no-repeat; } }

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto; }

.blockquote, blockquote {
  background-color: $white;
  font-style: italic;
  position: relative;
  padding: 30px;
  padding-left: 130px;
  position: relative;
  box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.05);
  @include md {
    padding: 20px;
    padding-left: 20px; }
  @include sm {
    padding: 10px; }
  .icon {
    position: absolute;
    z-index: 0;
    top: 10px;
    left: 45px;
    font-size: 45px;
    fill: #f4f4f4; }
  p {
    position: relative;
    z-index: 1; } }

.list {
  li {
    position: relative;
    padding-left: 25px;
    padding-right: 15px;
    @include notlast {
      margin-bottom: 15px;
      @include md {
        margin-bottom: 10px; } }
    @include after {
      color: $white;
      @include icon-arrow(0.625, 0.375, 0.06, currentColor, -45, em);
      display: inline-block;
      z-index: 1;
      position: absolute;
      top: .45em;
      left: .35em; }
    @include before {
      position: absolute;
      border-radius: 50%;
      background-color: $accent;
      top: .1em;
      left: 0;
      z-index: 0;
      width: 1.25em;
      height: 1.25em; } }
  &--half {
    ul {
      @include flex(flex-start, normal, row, wrap);
      @include sm {
        display: block; } }
    li {
      width: 50%;
      @include sm {
        width: auto; } } } }

.link {
  color: $accent;
  font-weight: 700;
  @include after {
    @include icon-arrow(0.625, 0.625, 0.06, currentColor, 225, em);
    display: inline-block;
    margin-left: 5px;
    position: relative; }
  &--bottom {
    @include after {
      @include rotate(-45);
      top: -.1em; } } }

.meta {
  color: #c3c3c3;
  @include flex(flex-start, center);
  font-size: 13px;
  &__item {
    @include notlast {
      margin-right: 15px; }
    .icon {
      margin-right: 5px;
      position: relative;
      top: .1em; } } }
