.contacts {
  &__content {
    height: 100%;
    @include flex(center, normal, column); } }

.contacts-block {
  &__title {
    color: $white;
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__text {
    color: $gray-light; }
  &__block-title {
    font-weight: 600;
    color: $white; }
  &__block {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } } }

.map {
  position: relative;
  padding-top: 50%;
  background-color: $gray-light;
  iframe {
    @include coverdiv; }
  @include xss {
    padding-top: 70%; } }
