.section {
  padding-top: 55px;
  padding-bottom: 55px;
  @include xl {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include lg {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include md {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    color: $black;
    text-align: center;
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__link {
    text-align: right;
    margin-bottom: 20px; }
  &__breadcrumbs {
    padding-top: 15px;
    padding-bottom: 55px;
    @include xl {
      padding-bottom: 50px; }
    @include lg {
      padding-bottom: 40px; }
    @include md {
      padding-bottom: 30px; } }

  &--pt-lg {
    padding-top: 110px;
    @include xl {
      padding-top: 100px; }
    @include lg {
      padding-top: 80px; }
    @include md {
      padding-top: 60px; } }
  &--pb-lg {
    padding-bottom: 110px;
    @include xl {
      padding-bottom: 100px; }
    @include lg {
      padding-bottom: 80px; }
    @include md {
      padding-bottom: 60px; } }
  &--pt-0 {
    padding-top: 0 !important; }
  &--bg {
    @include cover;
    position: relative;
    .container {
      position: relative;
      z-index: 1; }
    @include before {
      @include coverdiv;
      background-color: rgba($black, .8);
      z-index: 0; } }
  &--gray-lighten {
    background-color: $gray-lighten; } }

.section-img {
  @include cover;
  position: relative;
  @include before {
    z-index: -1;
    background-color: $gray-lighten;
    width: 100%;
    height: 100%;
    top: 20px;
    left: -20px;
    position: absolute; } }
