.slider {
  &__wrap {
    position: relative;
    padding-left: 100px;
    padding-right: 100px;
    @include md {
      padding-left: 50px;
      padding-right: 50px; } }
  &__prev,
  &__next {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%); }
  &__prev {
    left: 0; }
  &__next {
    right: 0;
    transform: translate(0, -50%) scaleX(-1); }

  &--cert {
    &__wrap {
      .tns-ovh {
        padding-top: 20px;
        padding-bottom: 20px; }
      .slide {
        padding-top: 20%;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 50% 50%;
        position: relative;
        .no-touch & {
          filter: blur(1px); }
        @include tr(transform .3s, filter .3s);
        @include hover {
          @include scale(1.1);
          filter: blur(0);
          .slide__icon {
            visibility: visible;
            opacity: 1; } }
        &__icon {
          @include vcenter;
          z-index: 1;
          background-color: $accent;
          border-radius: 50%;
          width: 70px;
          height: 70px;
          color: $white;
          @include flex(center, center);
          opacity: 0;
          visibility: hidden;
          @include tr(opacity .3s, visibility .3s);
          .icon {
            font-size: 30px; } } } } } }


.prev, .next {
  @include flex(center, center);
  background-color: $white;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.11);
  border-radius: 50%;
  width: 45px;
  height: 45px;
  color: $black;
  @include before {
    @include icon-arrow(10, 10, 1, currentColor, 45);
    display: inline-block;
    margin-left: 4px; }
  @include md {
    width: 30px;
    height: 30px;
    @include before {
      width: 7px;
      height: 7px; } } }

.next {
  transform: scaleX(-1);
  &.slider__next {
    transform: translate(0, -50%) scaleX(-1); } }

