.nav {
  text-align: center;
  @include lg {
    text-align: left; }
  li {
    display: inline-block;
    margin-left: 25px;
    margin-right: 25px;
    @include lg {
      display: block;
      margin-bottom: 10px;
      margin-right: 0;
      margin-left: 0; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  .is-active {
    color: $accent; } }
