.services {
  @include flex(flex-start, normal, row, wrap); }

.service-item {
  $this: &;
  display: block;
  @include cover;
  position: relative;
  padding: 30px;
  padding-top: 25%;
  color: $white;
  border: 1px solid  $white;
  width: 50%;
  @include md {
    padding: 20px;
    padding-top: 25%; }
  @include sm {
    padding: 10px;
    padding-top: 25%; }
  @include xs {
    width: 100%;
    border-right: 0;
    border-left: 0;
    padding-top: 40%; }
  @include before {
    @include coverdiv;
    background-color: rgba($black, .4); }
  @include hover {
    #{$this}__cover {
      visibility: visible;
      opacity: 1; } }
  &__title {
 }    // font-weight: 600
  &__content {
    position: relative;
    z-index: 1; }
  &__cover {
    font-weight: 700;
    background-color: rgba($accent, .8);
    @include flex(center, center);
    @include coverdiv;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    @include tr(visibility .3s, opacity .3s);
    .icon {
      font-size: 30px;
      margin-right: 5px; } } }

.service-meta {
  @include flex(flex-start, center);
  @include md {
    display: block; }
  &__item {
    @include notlast {
      margin-right: 40px;
      @include lg {
        margin-right: 20px; }
      @include md {
        margin-right: 0;
        margin-bottom: 10px; } } }
  &__icon {
    color: $accent;
    display: inline-block;
    .icon {
      font-size: 1.3em;
      position: relative;
      margin-right: .2em;
      top: .2em; } } }
