.card {
  @include flex(flex-start, normal, column);
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0px 0px 11px 0px rgba(0, 0, 0, 0.11);
  height: calc(100% - 30px);
  margin-bottom: 30px;
  @include sm {
    margin-bottom: 15px;
    height: calc(100% - 15px); }
  @include xs {
    height: auto; }
  @include hover {
    .btn--transp {
      color: $white;
      background-color: $accent; } }
  &__img {
    padding-top: 80%;
    display: block;
    @include cover;
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__content {
    flex-grow: 1;
    @include flex(flex-start, normal, column);
    background-color: $white;
    padding: 20px;
    @include md {
      padding: 10px; } }
  &__text {
    flex-grow: 1;
    display: block;
    margin-bottom: 15px;
    @include lg {
      margin-bottom: 10px; } }
  &__title {
    margin-bottom: 15px;
    display: block;
    color: $c-text-dark;
    @include md {
      margin-bottom: 10px; } }
  &__btn {
    display: block; }
  &__meta {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } } }
