.popup {
  position: fixed;
  z-index: 20;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: auto;
  background-color: rgba($black, .7);
  padding: 15px;
  @include vertical;
  visibility: hidden;
  opacity: 0;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  &.is-active {
    visibility: visible;
    opacity: 1; }
  &__inner {
    display: inline-block;
    vertical-align: middle;
    font-size: $fz;
    width: 100%;
    background-color: $white;
    position: relative;
    max-width: 370px;
    border-radius: 20px;
    padding: 25px 55px;
    @include xl {
      padding: 25px;
      padding-top: 50px; }
    @include md {
      padding: 20px;
      padding-top: 50px; }
    @include sm {
      padding: 15px;
      padding-top: 50px;
      font-size: $fz-sm-px;
      border-radius: 10px; } }
  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    z-index: 1;
    top: 20px;
    right: 20px;
    @include tr(color .3s);
    @include hover {
      color: $black; } }
  &__img-lg {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    padding-top: 50%; } }
